import React, { useContext } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import  { useEffect, useReducer } from 'react'
import axios from 'axios'
import ListGroup from 'react-bootstrap/ListGroup'
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col'
import Rating from '../Components/Rating';
import Card from 'react-bootstrap/Card';
import Badge from 'react-bootstrap/Badge'
import ListGroupItem from 'react-bootstrap/esm/ListGroupItem'
import Button from 'react-bootstrap/esm/Button'
import { Store } from '../Store'



const reducer=(state,action)=>{
    switch(action.type){
      case "FETCH_REQUEST":
      return {...state,loading:true}
      case "FETCH_SUCCESS":
      return {...state,product:action.payload,loading:false}
      case "FETCH_FAIL":
      return {...state,loading:false,error:action.payload}
      default:
      return state;
    }
  }
  

function ProductScreen() {
    const params =useParams();
    const navigate=useNavigate();
    const {slug}=params
    const [{loading,error,product},dispatch]=useReducer(reducer, {
        product:[],
        loading:true,
        error:""
       }
      )
      useEffect(()=>{
        
        const fetchData= async()=>{
          dispatch({type:"FETCH_REQUEST"})
        try{
          const result=await  axios.get(`/api/products/slug/${slug}`)
          dispatch({type:"FETCH_SUCCESS",payload:result.data})
        }catch(err){
         dispatch({type:"FETCH_FAIL",payload:err.message})
        }
    
    
          
          // setProducts(result.data)
        };
        fetchData()
        
      },[slug])

   const {state,dispatch:ctxDispatch}=useContext(Store);
   const{cart}=state;
      
      const addToCartHandler=async()=>{
        const existItem=cart.cartItems.find((x)=>x._id===product._id)
        const quantity=existItem?existItem.quantity+1:1;
        const {data}=await axios.get(`/api/products/${product._id}`)
        if(data.countInStock<quantity){
          window.alert("out of stock");
          return;
        }
            ctxDispatch({type:'CART_ADD_ITEM',payload:{...product,quantity}},
            navigate('/cart'))

      }
    return loading?(<div>loading...</div>)
        :error?(<div>{error}</div>)
        :(<div> 
<Row>
    <Col  lg={6} className= "mb-3" md={7}>
        <img className='img-large' src={product.images} alt={product.name}></img>
    </Col>
    <Col md={3}>
      <ListGroup variant="flush">
        <ListGroup.Item>
          <h1>{product.name}</h1>
        </ListGroup.Item>
        <ListGroup.Item>
          <Rating
            rating={product.rating}
            numReviews={product.numreview}>
          </Rating>
        </ListGroup.Item>
        <ListGroup.Item> price: {product.price}</ListGroup.Item>
        <ListGroup.Item>
          Description:<p>{product.description}</p>
        </ListGroup.Item>

      </ListGroup>
    </Col>
    <Col md={3}>
      <Card>
        <Card.Body>
          <ListGroup variant="flush">
            <ListGroup.Item>
              <Row>
                <Col>Price:</Col>
                <Col>{product.price}</Col>
              </Row>
            </ListGroup.Item>
            <ListGroup.Item>
              <Row>
                <Col>Status:</Col>
                <Col>{product.countInStock>0?(<Badge bg="success">in stock</Badge>)
                :(<Badge bg="danger">unavailable</Badge>)}</Col>
              </Row>
            </ListGroup.Item>
            {product.countInStock>0 &&(
              <ListGroup.Item>
                <div className='d-grid'>
                  <Button  onClick={addToCartHandler} variant="primary">Add to cart</Button>
                </div>
              </ListGroup.Item>
            )

            }

          </ListGroup>
        </Card.Body>
      </Card>
    </Col>
</Row>

        </div>)
    

 
}

export default ProductScreen